import axios from 'axios'
import qs from "qs"

let wx = window.wx;


let http = {
	post: '',
	get: '',
	getAccessToken: '',
	getJsapiTicket: ''
};

let host = 'http://www.xqdj.cn'

http.post = function (api, data) {
	let prams = qs.stringify(data);
	return new Promise((resolve,reject) => {
		
		axios.post(`${host}${api}`,prams,{
			// 单独配置
			withCredentials: true
		}).then((res) => {
			resolve(res);
		})
	})
}

http.get = function (api, data) {
	let prams = qs.stringify(data);
	return new Promise((resolve,reject) => {
		axios.get(`${host}${api}`,{
			// 单独配置
			withCredentials: true
		}, prams).then((res) => {
			resolve(res);
		})
	})
}








