http.getJspackage = async function() {
	let api = `http://www.xqdj.cn/api/user/jspackage`;
	axios.get(`${api}`).then((res) => {
		console.log(res.data)
		wx.config({
			...res.data,
			jsApiList: [
				"chooseWXPay",
				"openAddress"
			] // 必填，需要使用的JS接口列表
		})
	})
}



export default http;